import './AdminFacturaDetail.css'

import { observer } from 'mobx-react-lite'
import React, { useState } from 'react'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'

import { Page } from '../../components'
import LoaderAdmin from '../../components/LoaderAdmin'
import { RUTAS } from '../../constants'
import { useStores } from '../../models/root-store/root-store-context'
import { Factura } from '../../services/api'
import {DetailViewProps} from "../../helpers-ts";

const FileDownload = require('js-file-download')

const numeral = require('numeral')
const moment = require('moment-timezone')

export const AdminFacturaDetail: React.FC<DetailViewProps> = observer(({ history, match }) => {
  const rootStore = useStores()
  const [Factura, setFactura] = useState<Factura | null>()
  const [Loading, setLoading] = useState(true)
  const TIPO_FACTURA = history.location.pathname.split('/')[3]
  const idFactura = match.params.id

  const setup = () => {
    obtenerFactura(parseInt(idFactura))
  }

  React.useEffect(() => {
    setup()
  }, [idFactura])

  const obtenerFactura = (id: number) => {
    rootStore.environment.api.getFactura(id).then((result) => {
      if (result.kind === 'ok') {
        setFactura(result.data)
      }
      setLoading(false)
    })
  }

  const obtenerPdf = (id: number, numero: string) => {
    rootStore.environment.api.getFacturaPdf(id).then((result) => {
      if (result.kind === 'ok') {
        FileDownload(result.data, numero + '.pdf')
      }
      setLoading(false)
    })
  }

  const registrarPago = () => {
    // @ts-ignore
    history.push({ pathname: RUTAS.ADMIN_PAGO_PROVEEDOR_CREATE, factura: Factura })
  }

  return (
    <Page login_required admin>
      <LoaderAdmin visible={Loading} />
      {!Loading && Factura && (
        <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
          <div className="container-fluid background-white">
            <div className="row page-titles">
              <div className="col-md-5 col-12 align-self-center">
                <h3 className="text-themecolor">Factura número: {Factura.numero}</h3>
              </div>
              <div className="col-md-7 col-12 align-self-center d-none d-md-block">
                <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                  <li className="breadcrumb-item">
                    <a onClick={() => history.push('/admin/')}>Inicio</a>
                  </li>
                  <li className="breadcrumb-item">
                    <a
                      onClick={() =>
                        history.push(
                          TIPO_FACTURA === 'proveedor' ? RUTAS.ADMIN_FACTURA_PROVEEDOR : RUTAS.ADMIN_FACTURA_CLIENTE,
                        )
                      }
                    >
                      {' '}
                      Factura {TIPO_FACTURA}
                    </a>
                  </li>
                  <li className="breadcrumb-item active">{Factura.numero}</li>
                </ol>
              </div>
            </div>
            {Factura.pdf_archivo && (
              <div className={'row'}>
                <div className={'col-12'}>
                  <div className={'text-right mb-2'}>
                    <div className={'btn-group ml-auto'}>
                      <div className={'text-right'}>
                        <a
                          className={'btn btn-primary text-white'}
                          title={'Descargar factura'}
                          onClick={() => obtenerPdf(Factura.id, Factura.numero)}
                        >
                          <FIcon.FileText size={14} style={{ marginBottom: 3 }} /> Descargar
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            )}
            {Factura.pendiente > 0 && !Factura.contado && (
              <div className={'row'}>
                <div className={'col-12'}>
                  <div className={'text-right mb-2'}>
                    <div className={'btn-group ml-auto'}>
                      <div className={'text-right'}>
                        <a
                          className={'btn btn-primary text-white'}
                          title={'Registrar pago'}
                          onClick={() => registrarPago()}
                        >
                          <FIcon.DollarSign size={14} style={{ marginBottom: 3 }} /> Registrar pago
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            )}
            <div className="card">
              <div className="card-body">
                <div className={'row'}>
                  <div className="col-3 text-primary">
                    <strong>Timbrado</strong>
                    <p className={'mt-2'}>{Factura.timbrado}</p>
                  </div>
                  <div className="col-3 text-primary">
                    <strong>Número de factura</strong>
                    <p className={'mt-2'}>{Factura.numero}</p>
                  </div>
                  <div className="col-3 text-primary">
                    <strong>Fecha</strong>
                    <p className={'mt-2'}>{moment(Factura.fecha).format('DD/MM/YYYY')}</p>
                  </div>
                  <div className="col-3 text-primary">
                    <strong>Forma de pago</strong>
                    <p className={'mt-2'}>{Factura.contado ? 'Contado' : 'Crédito'}</p>
                  </div>

                  <div className="col-3 text-primary">
                    <strong>RUC</strong>
                    <p className={'mt-2'}>{Factura.ruc}</p>
                  </div>
                  <div className="col-3 text-primary">
                    <strong>Razón social</strong>
                    <p className={'mt-2'}>{Factura.razon_social}</p>
                  </div>
                  <div className="col-6 text-primary">
                    <strong>Dirección</strong>
                    <p className={'mt-2'}>{Factura.direccion}</p>
                  </div>
                  <div className="col-3 text-primary">
                    <strong>Total IVA</strong>
                    <p className={'mt-2'}>Gs. {numeral(Factura.total_iva).format('0,0')}</p>
                  </div>
                  <div className="col-3 text-primary">
                    <strong>Total Exentas</strong>
                    <p className={'mt-2'}>Gs. {numeral(Factura.total_exentas).format('0,0')}</p>
                  </div>
                  <div className="col-3 text-primary">
                    <strong>Total IVA 5%</strong>
                    <p className={'mt-2'}>Gs. {numeral(Factura.total_iva_5).format('0,0')}</p>
                  </div>
                  <div className="col-3 text-primary">
                    <strong>Total IVA 10%</strong>
                    <p className={'mt-2'}>Gs. {numeral(Factura.total_iva_10).format('0,0')}</p>
                  </div>
                  <div className="col-3 text-primary">
                    <strong>Total</strong>
                    <p className={'mt-2'}>Gs. {numeral(Factura.total).format('0,0')}</p>
                  </div>
                  <div className="col-3 text-primary">
                    {!Factura.contado && <strong>Saldo pendiente</strong>}
                    {!Factura.contado && <p className={'mt-2'}>Gs. {numeral(Factura.pendiente).format('0,0')}</p>}
                  </div>
                </div>
              </div>
            </div>
            <h4 className={'my-3'}>Detalles</h4>
            <div className="card">
              <div className="card-body p-0 px-3">
                <div className={'row'}>
                  <div className="col-1 text-primary border-detalle-factura">
                    <p className={'py-3'}>
                      <strong>Cantidad</strong>
                    </p>
                  </div>
                  <div className="col-3 text-primary border-detalle-factura">
                    <p className={'py-3'}>
                      <strong>Descripción</strong>
                    </p>
                  </div>
                  <div className="col-2 text-primary border-detalle-factura">
                    <p className={'py-3'}>
                      <strong>Precio unitario</strong>
                    </p>
                  </div>
                  <div className="col-2 text-primary border-detalle-factura">
                    <p className={'py-3'}>
                      <strong>Exentas</strong>
                    </p>
                  </div>
                  <div className="col-2 text-primary border-detalle-factura">
                    <p className={'py-3'}>
                      <strong>5%</strong>
                    </p>
                  </div>
                  <div className="col-2 text-primary border-detalle-factura">
                    <p className={'py-3'}>
                      <strong>10%</strong>
                    </p>
                  </div>
                </div>
                {Factura.detalles.map((factura_detalle) => {
                  return (
                    <div className={'row'}>
                      <div className="col-1 text-primary border-detalle-factura">
                        <p className={'mt-2'}>{numeral(factura_detalle.cantidad).format('0,0')}</p>
                      </div>
                      <div className="col-3 text-primary border-detalle-factura">
                        <p className={'mt-2'}>{factura_detalle.descripcion}</p>
                      </div>
                      <div className="col-2 text-primary border-detalle-factura">
                        <p className={'mt-2'}>Gs. {numeral(factura_detalle.precio_unitario).format('0,0')}</p>
                      </div>
                      <div className="col-2 text-primary border-detalle-factura">
                        <p className={'mt-2'}>Gs. {numeral(factura_detalle.exentas).format('0,0')}</p>
                      </div>
                      <div className="col-2 text-primary border-detalle-factura">
                        <p className={'mt-2'}>Gs. {numeral(factura_detalle.iva_5).format('0,0')}</p>
                      </div>
                      <div className="col-2 text-primary border-detalle-factura">
                        <p className={'mt-2'}>Gs. {numeral(factura_detalle.iva_10).format('0,0')}</p>
                      </div>
                    </div>
                  )
                })}
                <div className={'row'}>
                  <div className="col-12 text-primary border-detalle-factura">
                    <p className={'pt-3'}>
                      <strong>Total: Gs. {numeral(Factura.total).format('0,0')}</strong>
                    </p>
                  </div>
                </div>
              </div>
            </div>
            {Factura.pagos.length > 0 && <h4 className={'my-3'}>Pagos</h4>}
            {Factura.pagos.length > 0 && (
              <div className="card">
                <div className="card-body p-0 px-3">
                  <div className={'row'}>
                    <div className="col-1 text-primary border-detalle-factura">
                      <p className={'py-3'}>
                        <strong>Fecha</strong>
                      </p>
                    </div>
                    <div className="col-5 text-primary border-detalle-factura">
                      <p className={'py-3'}>
                        <strong>Descripción</strong>
                      </p>
                    </div>
                    <div className="col-3 text-primary border-detalle-factura">
                      <p className={'py-3'}>
                        <strong>Monto</strong>
                      </p>
                    </div>
                    <div className="col-3 text-primary border-detalle-factura">
                      <p className={'py-3'}>
                        <strong>Medio de pago</strong>
                      </p>
                    </div>
                  </div>
                  {Factura.pagos.map((pago) => {
                    return (
                      <div className={'row'}>
                        <div className="col-1 text-primary border-detalle-factura">
                          <p className={'mt-2'}>{moment(pago.fecha_hora).format('DD/MM/YYYY')}</p>
                        </div>
                        <div className="col-5 text-primary border-detalle-factura">
                          <p className={'mt-2'}>{pago.descripcion}</p>
                        </div>
                        <div className="col-3 text-primary border-detalle-factura">
                          <p className={'mt-2'}>Gs. {numeral(pago.monto).format('0,0')}</p>
                        </div>
                        <div className="col-3 text-primary border-detalle-factura">
                          <p className={'mt-2'}>{pago.medio_pago}</p>
                        </div>
                      </div>
                    )
                  })}
                  <div className={'row'}>
                    <div className="col-12 text-primary border-detalle-factura">
                      <p className={'pt-3'}>
                        <strong>
                          Total: Gs.{' '}
                          {numeral(
                            Factura.pagos.reduce(function (a, b) {
                              return a + b['monto']
                            }, 0),
                          ).format('0,0')}
                        </strong>
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>
      )}
    </Page>
  )
})

export default AdminFacturaDetail
